<template>
  <v-container class="d-flex flex-column px-0" app v-touch:swiperight="rightSlide">
    <v-app-bar fixed class="top" app color="white">
      <v-btn fab text small @click="$router.back(-1)" class="mt-2">
        <v-icon large>mdi-arrow-left</v-icon>
      </v-btn>
      <span class="text_middle mt-2">过滤设置</span>
    </v-app-bar>
    <div class="container_myConfig">
      <v-list two-line>
        <div v-for="(item,i) in items" :key="i">
          <v-list-item @click="openShieldDrawer(i)">
            <v-list-item-avatar>
              <v-icon>{{item.icon}}</v-icon>
            </v-list-item-avatar>
            <v-list-item-content>
              <v-list-item-title>{{item.title}}</v-list-item-title>
              <v-list-item-subtitle>{{item.intro}}</v-list-item-subtitle>
            </v-list-item-content>
          </v-list-item>
          <v-divider></v-divider>
        </div>
      </v-list>
      <div class="config_intro text_small-">
        <v-icon color="primary">mdi-tag-text</v-icon>
        <span class="text_thick text_middle--">折叠与屏蔽说明</span>
        <div class="text_small--" v-html="filterMainContent"></div>
      </div>
    </div>
    <v-navigation-drawer fixed bottom dark v-model="shield.showShieldDrawer" touchless>
      <v-card flat style="background-color:#363636">
        <v-card-title>
          <v-icon>{{shield.icon}}</v-icon>
          <span class="ml-1">{{shield.title}}</span>
        </v-card-title>
        <v-divider></v-divider>
        <!-- 根据标签折叠帖子 -->
        <v-card-text v-if="shield.title=='根据标签折叠 / 屏蔽帖子'">
          <div>
            <span>我们将会折叠 / 屏蔽加了对应标签的帖子</span>
            <v-checkbox v-model="shieldTag.tags" label="性相关" value="性相关" color="primary"></v-checkbox>
            <v-checkbox
              v-model="shieldTag.tags"
              label="令人不适"
              value="令人不适"
              color="primary"
              class="mt-n3"
            ></v-checkbox>
            <v-checkbox
              v-model="shieldTag.tags"
              label="政治相关"
              value="政治相关"
              color="primary"
              class="mt-n3"
            ></v-checkbox>
            <v-checkbox
              v-model="shieldTag.tags"
              label="引战"
              value="引战"
              color="primary"
              class="mt-n3"
            ></v-checkbox>
            <v-checkbox
              v-model="shieldTag.tags"
              label="未经证实"
              value="未经证实"
              color="primary"
              class="mt-n3"
            ></v-checkbox>
          </div>
        </v-card-text>
        <!-- 根据词汇折叠帖子 -->
        <v-card-text v-if="shield.title=='根据词汇折叠 / 屏蔽帖子与评论'" class="pb-0 mb-0">
          <div>
            <span>
              我们将会折叠 / 屏蔽含有对应词汇的帖子和评论
              <br />
            </span>
            <span>
              <strong>『说明』</strong>
              <br />
            </span>
            <span>
              在分词时候采用了隐马尔科夫模型+动态词典
              <br />
            </span>
            <span>
              有一定的新词预测能力
              <br />
            </span>
            <span>但是开发者技术和数据集有限,仍然会出现一定比例的错误匹配现象</span>
            <br />
            <span>
              所以屏蔽词请
              <strong>尽量避免一个字的词</strong>,还望用户谅解
            </span>
          </div>

          <v-combobox
            class="mt-4"
            v-model="shieldWord.word"
            hide-selected
            label="输入需要屏蔽的词汇"
            multiple
            small-chips
            solo
          >
            <template v-slot:selection="{ attrs, item, parent }">
              <v-chip
                v-if="item === Object(item)"
                v-bind="attrs"
                :color="`${item.color} darken-1`"
                label
                small
              >
                <span class="pr-2">{{ item.text }}</span>
                <v-icon small @click="parent.selectItem(item)">mdi-close</v-icon>
              </v-chip>
            </template>
          </v-combobox>
        </v-card-text>
        <!-- 根据举报比折叠帖子 -->
        <v-card-text v-if="shield.title=='根据举报数折叠 / 屏蔽帖子'" class="mb-0 pb-0">
          <div v-html="filterRuleContent"></div>
          <v-radio-group v-model="shieldPostDislike.method" row class="mb-0 pb-0">
            <v-radio label="举报数 / 浏览量 >0.1 " value="1" color="primary"></v-radio>
            <v-radio label="举报数 / 浏览量 >0.2 " value="2" color="primary"></v-radio>
            <v-radio label="举报数 / 浏览量 >0.3 " value="3" color="primary"></v-radio>
            <v-radio label="举报数 / 浏览量 >0.4 " value="4" color="primary"></v-radio>
            <v-radio label="举报数 / 浏览量 >0.5 " value="5" color="primary"></v-radio>
            <div class="d-flex flex-wrap">
              <v-radio label="举报数 > 5  " value="6" color="primary"></v-radio>
              <v-radio label="举报数 > 10 " value="7" color="primary"></v-radio>
              <v-radio label="举报数 > 20 " value="8" color="primary"></v-radio>
            </div>
          </v-radio-group>
        </v-card-text>
        <v-card-text v-if="shield.title=='根据举报数折叠 / 屏蔽评论'" class="mb-0 pb-0">
          <div>
            <span>我们将根据您自定义的举报数来屏蔽或折叠对应评论。</span>
            <br />
            <span>
              <strong>『说明』</strong>
            </span>
            <br />
            <span>
              由于开发时间和技术原因，
              <br />暂时没有提供举报数 / 浏览量过滤评论的过滤方式。
            </span>
          </div>
          <v-radio-group v-model="shieldCommentDislike.method" column class="mb-0 pb-0">
            <v-radio label="举报数 > 5  " value="1" color="primary"></v-radio>
            <v-radio label="举报数 > 10 " value="2" color="primary"></v-radio>
            <v-radio label="举报数 > 20 " value="3" color="primary"></v-radio>
            <v-radio label="举报数 > 40 " value="4" color="primary"></v-radio>
            <v-radio label="举报数 > 80 " value="5" color="primary"></v-radio>
          </v-radio-group>
        </v-card-text>
        <v-card-actions class="mt-n4">
          <v-spacer></v-spacer>
          <v-radio-group v-model="method[shield.type-1]" row class="mr-2">
            <v-radio label="折叠" value="1" color="primary"></v-radio>
            <v-radio label="屏蔽" value="2" color="primary"></v-radio>
          </v-radio-group>
          <v-btn color="primary" class="mr-2" @click="updateConfig(shield.type)">
            <v-icon>mdi-send</v-icon>
            <span>提交</span>
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-navigation-drawer>
  </v-container>
</template>

<script>
import touch from '../../utils/touch'
function randmonColor() {
  let colors = ['green', 'purple', 'indigo', 'cyan', 'teal', 'orange', 'red', 'deep-purple', 'blue', 'green', 'lime', 'amber']
  return colors[Math.floor(Math.random() * 12)]
}
const deFaultConfig = {
  shieldTag: {
    tags: ['性相关', '令人不适', '政治相关', '引战', '未经证实']
  },
  shieldWord: {
    word: [],
    way: 0
  },
  shieldPostDislike: {
    method: '1'
  },
  shieldCommentDislike: {
    method: '3'
  },
  method: ['1', '1', '1', '1']
}
export default {
  name: 'myCollect',
  data() {
    return {
      shield: {
        showShieldDrawer: false,
        title: '',
        icon: '',
        type: 1
      },
      items: [
        {
          title: '根据标签折叠 / 屏蔽帖子',
          icon: 'mdi-tag-remove-outline',
          intro: '折叠 / 屏蔽加了对应标签的帖子'
        },
        {
          title: '根据举报数折叠 / 屏蔽帖子',
          icon: 'mdi-sticker-remove-outline',
          intro: '折叠 / 屏蔽举报较多的帖子'
        },
        {
          title: '根据举报数折叠 / 屏蔽评论',
          icon: 'mdi-comment-remove-outline',
          intro: '折叠 / 屏蔽举报较多的评论'
        },
         {
          title: '根据词汇折叠 / 屏蔽帖子与评论',
          icon: 'mdi-alpha-a-box-outline',
          intro: '折叠 / 屏蔽主贴里有对应词汇的帖子和评论'
        },
      ],
      shieldTag: {
        tags: []
      },
      shieldWord: {
        word: [],
        way: 0
      },
      shieldPostDislike: {
        method: '1'
      },
      shieldCommentDislike: {
        method: '3'
      },
      method: ['1', '1', '1', '1'],
      originWords: [],
      filterRuleContent: '',
      filterMainContent: ''
    }
  },
  created() {
    let that = this
    that.$request('get', '/user/getConfig', {}, {})
      .then(res => {
        res = res.data

        try {
          this.shieldTag.tags = JSON.parse(res.shieldTag)
          this.shieldWord.word = JSON.parse(res.shieldPostWord)
          this.shieldPostDislike.method = String(res.shieldPostDislike)
          this.shieldCommentDislike.method = String(res.shieldCommentDislike)
          this.method = [String(res.shieldTagMethod), String(res.shieldPostDislikeMethod), String(res.shieldCommentDislikeMethod),String(res.shieldPostWordMethod)]
          that.originWords = JSON.parse(res.shieldPostWord)
        }
        catch (e) {
          setTimeout(() => {
            that.showSnackbar('获取设置失败,显示原始设置')
            for (let key in deFaultConfig) {
              that[key] = deFaultConfig[key]
            }
            that.originWords = deFaultConfig.shieldWord.word
          }, 1000);
        }
      })
      .catch(e => {
        setTimeout(() => {
          that.showSnackbar('获取设置失败,显示原始设置')
          for (let key in deFaultConfig) {
            that[key] = deFaultConfig[key]
          }
          that.originWords = deFaultConfig.shieldWord.word
        }, 1000);
      });
    that.$request('get', '/user/getFilterMainContent', {}, {}).then(res => {
      that.filterMainContent = res.data.filterMainContent; that.filterRuleContent = res.data.filterRuleContent
    }).catch(e => { that.showSnackbar('获取说明内容失败') })
  },
  methods: {
    openShieldDrawer(i) {
      let that = this
      that.shield.showShieldDrawer = true
      that.shield.title = that.items[i].title
      that.shield.icon = that.items[i].icon
      that.shield.type = i + 1
    },
    updateConfig(type) {
      let that = this
      if (type == 1) {
        that.$request('post', '/user/updateConfig', {}, {
          shieldTag: this.shieldTag.tags,
          shieldTagMethod: this.method[type - 1]
        })
          .then(res => { that.showSnackbar('更新设置成功!', 'ok') })
          .catch(e => { console.log(e); that.showSnackbar('更新设置失败') })
      }
      if (type == 2) {
        that.$request('post', '/user/updateConfig', {}, {
          shieldPostDislike: this.shieldPostDislike.method,
          shieldPostDislikeMethod: this.method[type - 1]
        })
          .then(res => { that.showSnackbar('更新设置成功!', 'ok') })
          .catch(e => { console.log(e); that.showSnackbar('更新设置失败') })
      }
      if (type == 3) {
        that.$request('post', '/user/updateConfig', {}, {
          shieldCommentDislike: this.shieldCommentDislike.method,
          shieldCommentDislikeMethod: this.method[type - 1]
        })
          .then(res => { that.showSnackbar('更新设置成功!', 'ok') })
          .catch(e => { console.log(e); that.showSnackbar('更新设置失败') })
      }
      if (type == 4) {
        let words = this.shieldWord.word.map(item => item.text)
        that.$request('post', '/user/updateConfig', {}, {
          shieldPostWord: words,
          shieldPostWordMethod: this.method[type - 1]
        })
          .then(res => { that.showSnackbar('更新设置成功!', 'ok') })
          .catch(e => { console.log(e); that.showSnackbar('更新设置失败') })
      }
    },
    rightSlide() {
      this.$router.go(-1)
    }



  },
  watch: {
    "shield.showShieldDrawer"(v) {
      this.$store.change_drawer(v, "shieldDrawer")
    },
    "$store.state.drawer"(v) {
      if (this.$store.state.drawerName == 'shieldDrawer') {
        this.shield.showShieldDrawer = v
      }
    },
    'shieldWord.word'(val, prev) {
      if (val.length === prev.length) return
      if (val.slice(-1)[0].length > 10) {
        this.showSnackbar('不能超过十个字');
        this.shieldWord.word = val.slice(0, val.length - 1)
        return
      }
      if (val.length > 15) {
        this.showSnackbar('最多设置十五个屏蔽词');
        this.shieldWord.word = val.slice(0, val.length - 1)
        return
      }
      this.shieldWord.word = val.map(v => {
        if (typeof v === 'string') {
          v = {
            text: v,
            color: randmonColor(),
          }
        }

        return v
      })
    },
  },
  directives: {
    touch
  }
}
</script>

<style>
.container_myConfig {
  margin-top: 40px;
}
.config_intro {
  width: 90%;
  margin: auto;
  margin-top: 12px;
}
</style>


